import {
  scrollTop,
  assert,
  downloadFile,
  smalltoBIG
} from '../utils/tools';
import Oss from './oss';
import Vue from 'vue';
const { ossUpload } = Oss

/**
 * 
 * 
 *
 * @param {*} obj
 * @param {Array<Object>} cache
 * @return {*}
 */
export function deepCopy(obj, cache = []) {
  if (obj === null || typeof obj !== 'object') {
    return obj
  }

  const hit = find(cache, c => c.original === obj)
  if (hit) {
    return hit.copy
  }

  const copy = Array.isArray(obj) ? [] : {}
  cache.push({
    original: obj,
    copy
  })

  Object.keys(obj).forEach(key => {
    copy[key] = deepCopy(obj[key], cache)
  })

  return copy
}

/**
 *  
 *  @obj
 **/
let dataType = obj => {
  if (obj === null) return 'Null'
  if (obj === undefined) return 'Undefined'
  return Object.prototype.toString.call(obj).slice(8, -1)
}

/**
 * null
 * @param {Object} obj 
 * @param {Array} AllowNull 
 */
export const dealObjectValue = (obj, AllowNull) => {
  // console.log(AllowNull);
  if (Array.isArray(obj)) {
    return obj
  }
  var param = {}
  if (obj === null || obj === undefined || obj === '') return param
  for (var key in obj) {
    if (dataType(obj[key]) === 'Object') {
      param[key] = dealObjectValue(obj[key])
    } else if ((obj[key] !== null && obj[key] !== undefined && obj[key] !== '' && !Array.isArray(obj[key])) || (Array.isArray(obj[key]) && obj[key].length > 0)) {
      param[key] = obj[key]
    }
    if (AllowNull) {
      AllowNull.map(key => {
        param[key] = obj[key] != null ? obj[key] : ''
      })
    }
    if (param[key] && typeof param[key] === 'string') {
      param[key] = obj[key].trim()
    }
  }
  return param
}


// 
let firstCase = str => {
  return str.substring(0, 1).toUpperCase() + str.substring(1)
}

// 
let firstLower = str => {
  return str.substring(0, 1).toLowerCase() + str.substring(1)
}

// 
let showWarning = (content, cb) => {
  return Vue.prototype
    .$confirm(content, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
    .then(() => {
      cb()
    })
    .catch(() => { })
}


export default {
  dataType,
  dealObjectValue,
  firstCase,
  showWarning,
  firstLower,
  deepCopy,
  scrollTop,
  assert,
  downloadFile,
  smalltoBIG,
  ossUpload
}
